
























import { Component, Vue } from 'vue-property-decorator';
import MenuDrawer from './MenuDrawer.vue';

@Component({
  components: {
    MenuDrawer,
  },
})
export default class MenuManage extends Vue {
  public columns: any = [
    {
      title: '菜单名称',
      dataIndex: 'component_name',
      align: 'center',
      ellipsis: true,
    },
    {
      title: '图标',
      dataIndex: 'icon',
      align: 'center',
      ellipsis: true,
    },
    {
      title: '组件路径',
      dataIndex: 'component',
      align: 'center',
      ellipsis: true,
    },
    {
      title: 'URL路径',
      dataIndex: 'component_url',
      align: 'center',
      ellipsis: true,
    },
    {
      title: '排序',
      dataIndex: 'sortNo',
      align: 'center',
      ellipsis: true,
    },
    {
      title: '操作',
      dataIndex: 'edit',
      scopedSlots: { customRender: 'edit' },
      align: 'center',
    },
  ];
  public dataSource: any = [
    {
      key: 1,
      component_name: 'John Brown',
      icon: '￥300,000.00',
      component: 'New York No. 1 Lake Park',
      component_url: 'New York No. 1 Lake Park',
      sortNo: '1',
    },
  ];

  public MenuDrawer: any = '';

  // 编辑按钮样式
  public editStyle(edit: string): string {
    return 'color: #1890ff;cursor: pointer;';
  }

  // 计算属性
  public get rowSelection(): any {
    return {
      onChange: (selectedRowKeys: any, selectedRows: any) => {
        // console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: (record: any) => ({
        props: {
          disabled: false,
        },
      }),
    };
  }

  // 点击编辑时间
  public clickEditDetail(): void {
    this.$nextTick(() => {
      this.MenuDrawer.showDrawer();
    });
  }

  public onThis(thiz: any): void {
    this.MenuDrawer = thiz;
  }

}
