

















import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {},
})
export default class MenuDrawer extends Vue {
    public visible: boolean = false;

    public afterVisibleChange(val: any): void {
      // console.log('visible', val);
    }

    public showDrawer(): void {
      this.visible = true;
    }

    public onClose(): void {
      this.visible = false;
    }

    public mounted(): void {
      this.$emit('onThis', this);
    }
}
